<template>
  <div>
    <b-table
        hover v-if="tableti.length"
        sticky-header="800px"
        :items="tableti"
        :fields="fields"
        head-variant="light"
        @row-clicked="editTablet">
      <template v-slot:cell(delete)="row">
        <b-button v-if="admin == false" variant="danger" @click="delete_tablet(row.item.id)">Delete</b-button>
      </template>
    </b-table>
    <h1 v-else>No tablets to show</h1>
  </div>
</template>

<script>
import router from "@/router";
import { mapState, mapActions } from 'vuex';

export default {
  name: "TabletList",
  computed: {
    ...mapState(['tableti','admin'])
  },
  data() {
    return {
      fields: [
        { key: 'marka' },
        { key: 'model' },
        { key: 'procesor' },
        { key: 'verzija' },
        { key: 'delete' }
      ]
    }
  },
  methods: {
    ...mapActions(['delete_tablet']),

    editTablet: function (item, index, event) {
      if(this.admin == false){
        router.push({path: `/tablet/${item.id}`});
      }

    }
  }
}
</script>

<style>
tr:hover td{
  background: goldenrod;
}
</style>